  "21200": "The Elegant Systems Architect: You phrase requirements like a formal white paper—intellectually dense and technically precise. Your patience is rock-solid; you're happy to guide the AI through a production minefield with the calm of a Zen master. You're an elegant mind building a fortress of logic.",
